}
.WordBook .TopRight {
  border-top-right-radius: 50px 50px;
}
.WordBook .BottomLeft {
  border-bottom-left-radius: 50px 50px;
}
.WordBook .BottomRight {
  border-bottom-right-radius: 50px 50px;
}

.WordBook2 {
  display: contents;
}

.WordBook2 .BookCard:nth-child(1) {
