}
.WordBook2 .BookCard:nth-child(2) {
  grid-area: 1/5/5/9;
}
.WordBook2 .BookCard:nth-child(3) {
  grid-area: 1/9/5/13;
}
.WordBook2 .BookCard:nth-child(4) {
  grid-area: 5/1/9/5;
}
.WordBook2 .BookCard:nth-child(5) {
  grid-area: 5/5/9/9;
}
.WordBook2 .BookCard:nth-child(6) {
  grid-area: 5/9/9/13;
}
.WordBook2 .BookCard:nth-child(7) {
  grid-area: 9/1/13/5;
}
.WordBook2 .BookCard:nth-child(8) {
  grid-area: 9/5/13/9;
}
.WordBook2 .BookCard:nth-child(9) {
