}

.Main {
  display: contents;
}
.Main .Banner {
  grid-area: 1/1/3/13;
  text-align: center;
  line-height: 380px;
}
.Main .Slider {
  grid-area: 3/1/5/13;
  padding: 40px;
}
.Main .WordBook {
  grid-area: 5/2/7/12 !important;
  display: grid;
  grid-template-rows: repeat(2, minmax(40px, 1fr));
  grid-template-columns: repeat(3, minmax(40px, 1fr));
  grid-gap: 6px;
  gap: 6px;
  padding: 2% 10%;
}
.Main .Simulation {
  grid-area: 7/1/9/13;
  text-align: center;
  line-height: 380px;
}
.Main .WordShare {
  grid-area: 9/1/11/13;
  text-align: center;
  line-height: 380px;
}
.Main .Support {
  grid-area: 11/1/12/13;
  text-align: center;
  line-height: 190px;
}

.BackCard {
  grid-area: 2/2/12/12;
}

.Analyze {
  display: contents;
}
.Analyze .Title {
  grid-area: 2/2/3/12;
  text-align: center;
}
.Analyze .Upload {
  grid-area: 4/3/5/11;
}
.Analyze .FileName {
  grid-area: 5/3/6/11;
}
.Analyze .Level {
  grid-area: 6/3/7/11;
}
.Analyze .Amount {
  grid-area: 7/3/8/11;
}
.Analyze .Category {
  grid-area: 8/3/9/11;
}
.Analyze .Setting {
  grid-area: 9/4/11/10;
  text-align: center;
}
.Analyze .Result {
  grid-area: 11/10/12/11;
}
.Analyze .Result .Button {
  text-align: center;
}

/* default tags */
body {
  margin: 0;
  font-family: 'Noto Sans', sans-serif;
  word-break: keep-all;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
  /* -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}
button {
  background: none;
  border: none;
  font: unset;
  display: flex;
}
div {
  box-sizing: border-box;
}

/* scroll bar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}
::-webkit-scrollbar-track {
  background: #f1f1f1;
}
::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background: #aaaaaa;
}
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

/* link */
a:-webkit-any-link {
  cursor: pointer;
  text-decoration: none;
}

/* state */
/* focus */
:focus {
  outline: none;
}
:disabled {
  cursor: unset !important;
}

/* Custom Class */
/* Color */
.ColorWhite {
  color: white !important;
}
.ColorRed {
  color: #dc0019 !important;
}
.ColorBlue {
  color: #1976d2 !important;
}
.ColorYello {
  color: #ffab00 !important;
}
.ColorGreen {
  color: #4caf50 !important;
}

/* background */
.BackWhite {
  background-color: white !important;
}
.BackRed {
  background-color: #dc0019 !important;
}
.BackBlue {
  background-color: #1976d2 !important;
}
.BackYello {
  background-color: #ffab00 !important;
}
.BackGreen {
  background-color: #4caf50 !important;
}

/* Border */
.BorderWhite {
  border-color: white;
}
.BorderRed {
  border-color: #dc0019;
}
.BorderBlue {
  border-color: #1976d2;
}
.BorderYello {
  border-color: #ffab00;
}
.BorderGreen {
  border-color: #4caf50;
}

/* Opacity */
.Opacity3 {
  opacity: 0.3;
}
.Opacity5 {
  opacity: 0.5;
}
.Opacity8 {
  opacity: 0.8;
}

/* Size */
.Size10 {
  font-size: 10px;
}
.Size12 {
  font-size: 12px;
}
.Size14 {
  font-size: 14px;
}
.Size16 {
  font-size: 16px;
}
.Size21 {
  font-size: 21px;
}
.Size24 {
  font-size: 24px;
}
.Size28 {
  font-size: 28px;
}
.Size32 {
  font-size: 32px;
}
.Size40 {
  font-size: 40px;
}
.Size48 {
  font-size: 48px;
}

/* SizeRes */
.SizeRes10 {
  font-size: calc(10px + 1vmin);
}
.SizeRes12 {
  font-size: calc(12px + 1.5vmin);
}
.SizeRes14 {
  font-size: calc(14px + 1.5vmin);
}
.SizeRes16 {
  font-size: calc(16px + 1.5vmin);
}
.SizeRes21 {
  font-size: calc(21px + 2vmin);
}
.SizeRes24 {
  font-size: calc(24px + 2vmin);
}
.SizeRes28 {
  font-size: calc(28px + 2vmin);
}
.SizeRes32 {
  font-size: calc(32px + 2vmin);
}
.SizeRes40 {
  font-size: calc(40px + 2vmin);
}
.SizeRes48 {
  font-size: calc(48px + 2vmin);
}

/* Weight */
.Bold {
  font-weight: bold;
}

/* Radius */
.Radius2 {
  border-radius: 2px;
}
.Radius50 {
  border-radius: 50%;
}

/* Button */
.Button,
.ButtonDarken {
  border-radius: 2px;
  cursor: pointer;
  padding: 3px;
  font-family: 'Noto Sans', sans-serif;
  /* font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; */
}
.RoundButton,
.RoundButtonDarken {
  border-radius: 50%;
  cursor: pointer;
}

/* Hidden Contents */
.Hidden {
  display: none;
}

