.App {
  width: 100%;
  min-height: 100vh;
  display: grid;
  grid-template-rows: 56px minmax(1020px, calc(100% - 196px)) 140px;
  /* grid-template-columns: minmax(1254px, 1fr); */
}

.Header {
  grid-area: 1/1/2/1;
  align-items: center;
  justify-content: center;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 5;
}
.HeaderContent {
  display: grid;
  grid-template-columns: repeat(12, 72px);
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  justify-content: center;
}

.Header .Left {
  grid-column: 1/3;
  text-align: center;
}
.Header .Center {
  grid-column: 3/10;
}
.Header .Right {
  grid-column: 10/13;
  text-align: center;
}

.Body {
  grid-area: 1/1/12/13;
  display: contents;
}

.Background {
  grid-area: 2/1/3/1;
  display: grid;
  grid-template-rows: repeat(6, minmax(170px, 1fr));
}

.Page {
  grid-area: 2/1/3/1;
  display: grid;
  grid-template-rows: repeat(12, minmax(85px, 1fr));
  grid-template-columns: repeat(12, 72px);
  grid-column-gap: 30px;
  -webkit-column-gap: 30px;
          column-gap: 30px;
  justify-content: center;
}

.Footer {
  grid-area: 3/1/4/1;
}

